<template><div class="b_naoword i_english">

<div v-if="cur_lesson == 'alphabet-and-pronunciation'">
	<div :class="$css.title"> Alphabet and pronunciation </div>

	<p> The basic alphabet of Nao has: </p>
	<p> 5 vowels: <b>a i u e o </b> </p>
	<p> 14 consonants: <b>p t k b d g s c h f l m n j </b> </p>
	<p> Also, 4 more letters are used in loan words only: <b>y z v r  </b> </p>

	<p> The good news is: you’ll easily guess how most of them are pronounced! So let's just take a look at the ones that require the most attention: </p>

	<v-simple-table class="mb-4">
	<template v-slot:default>
	  <thead>
	    <tr>
	      <th v-for="cell in ['Letter', 'Pronunciation', 'Example']" > {{ cell }} </th>
	    </tr>
	  </thead>
	  <tbody>
	    <tr
	      v-for="row in [
	      	['c', 'like English <i>sh</i>', 'gocu'],
	      	['j', 'same as English <i>j</i>', 'joi'],
	      	['l', 'a light <i>l</i> (like in Spanish) is strongly preferred', 'lapal'],
	      	['r', 'a trill (like in Spanish) is preferred but other varieties are acceptable too', '\'rus'],
	      	['y', 'a schwa (like English <i>a</i> in <i>a</i>bout)', '\'kentyki']
	      ]"
	      :key="row[0]"
	    >
	      <td> <b> {{ row[0] }} </b> </td>
	      <td v-html="row[1]"> </td>
	      <td > <nao pronounce :o="row[2]"/> </td>
	    </tr>
	  </tbody>
	</template>
	</v-simple-table>

	<p> The rest of the letters have standard international pronunciation that you would expect. You can listen to more examples throughout the textbook. </p>

	<p> One letter corresponds to one sound. Each word is pronounced exactly the way it is written, letter by letter.</p>

	<p> The vowels <b>i</b> and <b>u</b> in combination with other vowels don’t make a syllable. They don’t get stressed and are pronounced shortly. For example: </p>

	<div class="pl-6 mb-4">
		<div> <nao pronounce o="joi"/> sounds like English <i>joy</i> </div>
		<div> <nao pronounce o="kau"/> sounds like English <i>cow</i> </div>
		<div> <nao pronounce o="niu"/> sounds like English <i>new</i> </div>
	</div>

	<p> In elementary words, the stress is on the first syllable. For example: </p>

	<v-flex d-flex flex-wrap mb-4 pl-7>
	<v-flex v-for="p in [
		{'f<i>a</i>ia': 'be on fire', 'ki<i>o</i>': 'sibling' },
		{'\'k<i>a</i>lifornia': 'California', '\'pu<i>e</i>rto-riko': 'Puerto Rico' },
	]" pa-2>
		<div v-for="w in Object.keys(p)">
			<span v-html="w"> </span>
			(= <span> {{ p[w] }}</span> )
		</div>
	</v-flex>
	</v-flex>

	<p> In compound words, the stress is semantic but usually happens to fall on the last elementary word: </p>

	<v-flex d-flex flex-wrap mb-4 pl-7>
	<v-flex v-for="p in [
		{'madisakoh<i>o</i>m': 'school', 'ladif<i>o</i>i': 'acceleration' },
		{'soinien<i>a</i>i': 'summer', 'kaneduf<i>a</i>ia': 'fire extinguisher' },
	]" pa-2>
		<div v-for="w in Object.keys(p)">
			<span v-html="w"> </span>
			(= <span> {{ p[w] }}</span> )
		</div>
	</v-flex>
	</v-flex>
</div>


<div v-if="cur_lesson == 'phrases'">
	<div :class="$css.title"> Phrases </div>

	<p> Here are some example phrases and sentences that can give you the general picture of what the language is like and how it works. </p>

	<PAT :pairs="phrases"/>
</div>


<div v-else-if="cur_lesson == 'simple-sentences'">

	<div :class="$css.title"> Making simple sentences </div>

	<p> The good news is that the word order in Nao is quite similar to English. </p>

	<PAT :pairs="{'me eat/drink rice': 'I eat rice'}"/>

	<p> 
	So as you can see, it’s simply Subject + Verb + Object. By default, the verb has no tense so the sentence above could mean
	<i> I ate rice </i>
		or
	<i> I am eating rice </i>
		or
	<i> I will eat rice </i>
	, etc. You just guess the tense from context. Now take a look at these sentences:
	</p>

	<PAT :pairs="{'dog see/look me' : 'A dog is looking at me.', '\'sara married \'piter' : 'Sara is married to Peter.'}"/>

	<p>
	In English, prepositions are often used in conjunction with verbs, whereas in Nao, prepositions are often built into the verb.
	Also, Nao doesn’t use anything like the articles <i>a/the</i> and the noun by default could be singular or plural, so for example,
	<span :class="$css.naoword"> dog </span> could mean <i> a/the dog(s) </i>.
	</p>

	<p> To make negation, just put <nao n="not"/> in front of the verb </p>

	<PAT :pairs="{'\'jon not eat/drink rice' : 'John doesn\'t eat rice.'}"/>

	<p> To turn the sentence into a question, put <nao pronounce n="QUESTION"/> in the beginning. </p>

	<PAT :pairs="{'QUESTION you happy' : 'Are you happy?'}"/>

	<p>
	Note that:
	<br>1. Nao doesn’t use capital letters, to minimize the number of different characters.
	<br>2. The sentences are separated with a dash (−) or hyphen (-) because without the capital letters, the dot is too small to separate sentences, which makes reading more difficult.
	When writing a text, starting the first sentence with a dash and ending the last sentence with a dash is optional.
	<br>3. Nao doesn’t use sentence finals like the question mark, exclamation mark, period or ellipsis. Using them is considered informal.
	</p>

	<PAT :pairs="{'− happy-meet − you named what −' : 'Hello. What is your name?'}"/>

	<p> The question mark is unnecessary because every question sentence contains <nao n="QUESTION"/>  and/or  <nao n="what"/>. The other sentence finals are not used because their usage differs dramatically from language to language. </p>

</div>



<div v-else-if="cur_lesson == 'opposite'">

	<div :class="$css.title"> Building the opposite </div>

	<p> One of the most powerful features of Nao is forming the opposite meanings. To change the meaning to the opposite,
	alternate <span :class="$css.naoword"> i / u </span>. For example: </p>

	<PAT :little_columns="[
		{'warm/hot': 'warm/hot', 'cold': 'cold' },
		{'female': 'female', 'male': 'male' },
		{'previous': 'previous', 'next': 'next' }
	]" />

	<p> Note that to construct a word with the opposite meaning, the vowel that is altered must be the last vowel in the elementary word. This means that, for instance, <nao o="dia"/> and <nao o="dua"/> cannot be opposites.

	<p> Also note that even if a word has <span :class="$css.naoword"> i / u </span> as the last vowel, it doesn’t mean that it has an opposite for sure. Many words just don’t have an opposite because it would make no sense. For example:
		<span :class="$css.naoword"> maus </span> (mouse) and <span :class="$css.naoword"> mais </span> (corn)
	are of course not opposites and it is obvious from their meaning that they cannot possibly have any opposite. </p>

	<p> As a natural result, this trick automatically works for compound words as well: </p>

	<PAT :little_columns="[
		{'color-completely-light': 'white', 'color-completely-dark': 'black'},
		{'day-previous': 'yesterday', 'day-next': 'tomorrow' },
		{'cause-in_state-deserve-good': 'reward', 'cause-in_state-deserve-bad': 'punish' },
		{'part-year-warm/hot': 'summer', 'part-year-cold': 'winter' },
	]" />


	<div :class="$css.title"> Opposite vs negation </div>

	<p> Some beginners mix up the opposite with a simple negation. In reality, in most cases those are two different things. Let’s take a look at three possible situations: </p>

	<v-simple-table class="mb-4">
	<template v-slot:default>
	  <thead>
	    <tr>
	      <th v-for="cell in ['Original word', 'Opposite', 'Negation']" class="text-left"> {{ cell }} </th>
	    </tr>
	  </thead>
	  <tbody>
	    <tr
	      v-for="(row,index) in [
			['dai = right side', 'dau = left side', 'nodai = not right side'],
			['bai = buy', 'bau = sell', 'nobai = not buy'],
			['pau = parent', 'pai = child', 'nopau = not parent'],
			['li = good', 'lu = bad', 'noli = not good'],
			['bul = strong', 'bil = weak', 'nobul = not strong'],
			['dei = true', 'deu = false', 'nodei = not true'],
			['mai = be allowed', 'mau = be forbidden', 'nomai = be not allowed'],
		]"
	      :key="row[0]"
	      :style="'background-color:' + (index < 3 ? '#a1c8fc' : index < 5 ? '#fcf7a1' : '#a4fca1')"
	    >
	      <td v-for="cell in row"> <b>{{ cell.split('=')[0] }}</b> <br v-if="$vuetify.breakpoint.xs"><span v-else>=</span> {{ cell.split('=')[1] }} </td>
	    </tr>
	  </tbody>
	</template>
	</v-simple-table>

	<p>
		<div>blue – the opposite and the negation are completely different</div>
		<div>yellow – the opposite and the negation are similar but not the same</div>
		<div>green – the opposite and the negation are exactly the same</div>
	</p>

	<p> You can find specific rules about building the opposite in the Full Set of Rules II.8 </p>
</div>

<div v-else-if="cur_lesson == 'word-structure'">
	<div :class="$css.title"> Word structure </div>
	
	<p> In Nao, each word is a sequence of one or more elementary words. The structure of an element is: <i>consonant</i> + <i>vowel(s)</i> + (optionally) <i>consonant</i>. Examples: </p>

	<p>
		<div v-for="w in ['sudaban', 'henodutua', 'pottomfes']">
			<nao pronounce :o="w"/> = <span v-for="(el,index) in $nao.disassemble(w)"> <span v-if="index != 0"> + </span> <nao :o="el"> </nao> </span>
		</div>
	</p>

	<p> So when you see a Nao word, you can always easily know its structure with 100% accuracy. And if you know the meanings of the elements and the grammar, you can always figure out the meaning of the compound. Hover to see the meanings of the elements. </p>

	<p>
		<div v-for="w in ['bukan', 'dagocua']">
			<nao pronounce :o="w"/> =
			<span v-for="(el,index) in $nao.disassemble(w)">
				<span v-if="index != 0"> + </span> <nao :o="el"> </nao>
			</span>
			= {{ $nao.tr_from_Nao(w).main_tr }}
		</div>
	</p>

	<p> So in Nao, you can make these words by yourself but you have to strictly follow the grammar rules. Step by step we will learn how to make compound words by ourselves. And if you don't want to make any words, that's fine too - you can just look them up in the dictionary. </p>

	<div :class="$css.title"> Apostrophe </div>

	<p> Elementary words that don’t have a standard structure must be marked with an apostrophe.  Also all proper names (the names of people, cities, etc) irregardless of the structure are written with an apostrophe: </p>

	<PAT :little_columns="[
		{'\'inet': 'internet', '\'kanin': 'rabbit', '\'banan': 'banana'},
		{'\'tom': 'Tom', '\'maiami': 'Miami', '\'basketbol': 'basketball'},
	]" />
</div>

<div v-else-if="cur_lesson == 'main-grammar-rule'">

	<div :class="$css.title"> Main grammar rule </div>

	<p> The word order in Nao is determined by one main rule:</p>
	<v-alert
	  color="blue-grey"
      dark
      dense
      :icon="$mdi.School"
      prominent
	> The description comes after what is being described </v-alert>
	<p>That rule is very important because it determines the word order in the whole language, as well as the order of the elements inside a compound. In linguistics that is called strict right branching or strict head-initial word order. Let's see what that means in practice: </p>

	<p> 1. Adjectives come after nouns (because adjectives describe nouns): </p>

	<PAT :pairs="{'building big' : 'big house', 'female PASSIVE-like-see/look':'beautiful girl' }"/>

	<p> 2. The combination <i>noun<sub>1</sub> noun<sub>2</sub></i> means <i>noun<sub>1</sub> of noun<sub>2</sub></i> (because the second noun is a description of the first noun): </p>

	<PAT :pairs="{'year act_of-begin-live' : 'year of birth', 'person-in_contact-good me':'friend of mine' }"/>

	<p> 3. The combination <i>noun verb</i> means <i>noun who does verb</i>: </p>

	<PAT :pairs="{'person work' : 'person who works', 'someone/something cover':'thing that covers' }"/>

	<p>Note that some nouns have an in-built link, in which case they will form a bond with the next noun/verb in their own way: </p>

	<PAT :pairs="{'tool fight' : 'weapon = a tool that one uses to fight', 'document allowed':'license = document that certifies that somebody is allowed to do smth' }"/>

	<p> 4. The object comes after the verb (because we describe the action by specifying an object): </p>

	<PAT :pairs="{'buy object-eat/drink' : 'buy food', 'cause-different all':'change everything' }"/>

	<p> Note that many verbs have an-built link/preposition: </p>

	<PAT :pairs="{'tense think' : 'be tense because of thinking', 'commit_crime go-inside':'trespassing = commit a crime by entering some place' }"/>

	<p> You can find the list of elementary words and full grammatical information about them <router-link to="/elements"> here </router-link>. </p>
</div>


<div v-else-if="cur_lesson == 'ke'">

	<div :class="$css.title"> Questions with ke </div>

	<p> The word <nao n="what"/> simply means <i>what</i>. Let’s see how we can use it in a sentence: </p>

	<PAT :pairs="{'you eat/drink what?' : 'What are you eating? (literally: you eat what)'}"/>

	<p> You can see that <nao n="what"/> is at the end of the sentence. It’s because we must keep the word order Subject + Verb + Object. You can think like this: <nao n="what"/> should be placed in the same place as the answer word. </p>

	<PAT :pairs="{'\'jon gin ke' : 'What is John eating?', '\'jon gin pot' : 'John is eating potatoes.'}" />

	<p> You can see that <nao n="what"/> (the question word) and <nao n="potato"/> (the answer word) are at the same position. More examples: </p>

	<PAT :pairs="{
		'female-that go that for_the_sake buy what' : 'She is going there in order to buy what?',
		'female-that go that for_the_sake buy potato' : 'She is going there in order to buy potatoes.',
		'what cause-feel-in_danger male-that' : 'What scared him?',
		'dencul nenabun muta' : 'Lightning scared him.'
	}" />


	<p> Even if the sentence already has <nao n="what"/>, you can additionally put the question particle ca in the beginning, it is optional. </p>

	<PAT :pairs="{'(QUESTION) you eat/drink what': 'What are you eating?'}" />

	<p> It can also be used in connection with nouns, meaning <i> what/which </i>. For example: </p>

	<PAT :pairs="{'pot ke' : 'which potatoes', 'dio ke' : 'what story'}" />

	<p> All questions words are derived from <nao n="what"/>. Most commons ones are: </p>

	<PAT :pairs="{
		'person-what' : '(what person) = who',
		'time-what' : '(what time) = when',
		'because-what' : '(because of what) = why',
		'be_located-what' : '(be located in/at what) = where',
		'quantity-what' : '(of what quantity) = how much/many'
	}" />
</div>



<div v-else-if="cur_lesson == 'tenses'">
	<div :class="$css.title"> Verb tenses </div>

	<p> As mentioned previously, all tenses are optional. The three basic tenses are: </p>

	<PAT line :pairs="{
		'NOW' : 'present',
		'FUTURE' : 'future',
		'PAST' : 'past',
	}" />

	<PAT line :pairs="{
		'me NOW-go' : 'I go',
		'me FUTURE-go' : 'I will go',
		'me PAST-go' : 'I went',
	}" />

	<p> Apart from that, there are also aspects that can help you describe the tense more precisely. </p>


	<p>
		<nao n="inside"/> is the continuous aspect, we stress that the action is happening at a certain moment or over a specific period of time. <br>
		<nao n="outside"/> is the instantaneous aspect, we stress that the action takes a short time and finishes fast.
	</p>

	<PAT :pairs="{
		'me inside-see/look female-that' : 'I am looking at her. (continuously, at a specific moment)',
		'me outside-see/look female-that' : 'I took a look at her. (the action finished fast)',
	}" />

	<p>
		<nao n="regularly"/> is the regular aspect, it means the action happens on a regular basis<br>
		<nao n="not_regularly"/> is the non-regular aspect, it means the action happened once or twice, no regularity
	</p>

	<PAT :pairs="{
		'me PAST regularly-eat/drink potato' : 'I used to eat potatoes. (regularly in the past)',
		'me PAST not_regularly-eat/drink potato' : 'I ate potatoes once or twice. (no regularity)',
	}" />


	<p>
		<nao n="inside-PAST"/> is the perfect aspect; it means the action happened before a certain point in time and we see the result of that action.<br>
		<nao n="inside-FUTURE"/> is the prospective aspect; it means the action is going to happen judged from the current circumstances.
	</p>

	<PAT :pairs="{
		'me inside-PAST eat/drink' : 'I have eaten. (and the result is seen now - I am full)',
		'inside-FUTURE PASSIVE-do water-sky' : 'It’s going to rain (it is evident, the clouds are dark)',
	}" />

	<p> <nao n="would"/> is roughly equivalent to <i>would</i> and it is used in conditional clauses: </p>

	<PAT :pairs="{
		'if me have money quantity-big, me would buy building' : 'If I had a lot of money, I would buy a house.',
	}" />

	<p> It is used to show that the speaker doesn’t seriously consider the situation as a possibility.
		<b>bui</b> can be placed in both clauses or in either of them.
 	</p>

	<p> We can combine them in this order: (<b>bui</b>) + time + aspect </p>

	<PAT :pairs="{
		'you PAST-inside eat/drink what' : 'What were you eating?',
		'from day-next me FUTURE-regularly move-for_the_sake-healthy': 'From tomorrow I will do physical exercises',
		'if you would-PAST cause-know to me, me would-PAST cause-more-find_easy you' : 'If you had told me, I would have helped you',
		'if me have act_of-work, me not would-NOW-inside-sit be_located object-be_located-NOW' : 'If I had a job, I wouldn’t be sitting here now.',
	}" />

	<p> Note that the time of a subordinate verb/clause is always relative to the time of the head verb/clause, which is different from formal English: </p>

	<PAT :pairs="{
		'male-that PAST speak CLAUSE FUTURE cause-more-find_easy' : 'He said he would (literally: will) help',
		'male-that PAST speak CLAUSE PAST notice some' : 'He said he had seen something',
	}" />

	<div :class="$css.title"> Imperative </div>

	<p> Originally we have the verb <nao n="ask" /> meaning <i>ask/order (smb to do smth)</i>: </p>

	<PAT :pairs="{
		'female-sibling ask me go place-buy' : '[My] sister asked me to go to the shop.',
	}" />

	<p> But what makes this verb special is that if you omit the subject and the object, then it works as the imperative: </p>

	<PAT :pairs="{
		'ask-go' : 'Go!',
		'ask-not-buy that': 'Don\'t buy that!',
	}" />

	<p> In that case effectively it is a shorthand for <nao n="me ask you" /> = <i>I ask you (to do smth) </i>. </p>
	<p>  Note that <nao n="ask" /> is obligatory to use! If you leave it out, the sentence will be affirmative: </p>

	<PAT :pairs="{
		'not-buy that': '[Someone from context] doesn\'t buy that!',
	}" />

	<p> Use <nao n="ask + PLURAL-me" /> to say <i>let’s</i>: </p>

	<PAT :pairs="{
		'ask PLURAL-me begin': 'Let’s begin!',
	}" />

</div>

<div v-else-if="cur_lesson == 'clauses'">

	<div :class="$css.title"> Clauses </div>

	<p> What is a clause? It’s basically a sentence inside another sentence. In English, we often put <i> that </i> in front of the clause. In Nao, we use <nao n="CLAUSE" />: </p>

	<PAT :pairs="{
		'\'jon know CLAUSE \'alis not work day-NOW' : 'John knows that Alice doesn’t work today.',
		'\'alis sad CLAUSE \'jon not eat/drink' : 'Alice is sad that John doesn’t eat.',
		'me see/look CLAUSE \'alis work' : 'I see/watch Alice working.',
		'QUESTION you want CLAUSE me go' : 'Do you want me to go?',
	}" />

	<p> Similarly with the English marker <i>that</i>, in Nao <nao n="CLAUSE" /> is optional when it is easy to guess. </p>
	<p> It is also possible to make a question clause using <nao n="QUESTION" />, equivalent to <i>whether / if</i> in English: </p>

	<PAT :pairs="{
		'\'jon know QUESTION \'alis work day-NOW' : 'John knows whether Alice works today.',
	}" />

	<p> Note that if the question clause has <nao n="what" />, then <nao n="QUESTION" /> is optional. </p>

	<PAT :pairs="{
		'me not understand (QUESTION) you want what' : 'I don’t understand what you want.',
		'female-that LITTLE-want-know (QUESTION) because-what person-all stop-speak' : 'She is wondering why everybody got silent'
	}" />

	<p> Of course the clauses can also describe a noun: </p>

	<PAT :pairs="{
		'mu pe (ke) pulua' : 'The man who shouted',
		'da pe me kan (ke)' : 'The person whom I saw',
		'dia pe (leke) mit te' : 'The day [I] met you',
		'female-adult CLAUSE me work with male-sibling person-what': 'The woman whose brother I work with',
	}" />

	<p> As you can see in the sentences above, <nao n="what" /> is similar to <i>which/who</i>. When the descriptive clause has it, it's called a clear link, otherwise  it is a vague link. Note that in descriptive clauses, <b>pe</b> is obligatory. </p>
</div>


<div v-else-if="cur_lesson == 'location-and-direction'">
	<div :class="$css.title"> pa </div>

	<p> Originally it is a verb meaning <i>to be located (in/at)</i>, so we can say: </p>

	<PAT :pairs="{'me be_located \'boston' : ' I am in Boston'}"/>

	<p> But we can also use it as an adverb: </p>

	<PAT :pairs="{'me work be_located \'boston' : 'I work in Boston'}"/>

	<p> So the word <nao n="be_located"/> will usually translate to English as <i>in/at</i>. </p>

	<p> To talk about the location with more precision, the following words might be useful: </p>

	<PAT :little_columns="[
		{'inside': 'inside', 'outside': 'outside' },
		{'front': 'front', 'backside': 'back' },
		{'right': 'right', 'left': 'left' }
	]" />

	<p> All of the above words are nouns, so they are often used along with <nao n="be_located"/>. </p>

	<PAT :pairs="{
		'stick_shaped-write be_located \'mesa' : 'The pen is located … the table (just guess the preposition).',
		'stick_shaped-write be_located top \'mesa' : 'The pen is on the table.',
		'stick_shaped-write be_located bottom \'mesa' : 'The pen is under the table.',
		'stick_shaped-write be_located inside \'mesa' : 'The pen is in the table (maybe in the drawer)',
	}" />

	<div :class="$css.title"> tu (to), ti (from) </div>

	<p> We can use that pair to talk about directions. For example: </p>

	<PAT :pairs="{
		'go-fast to place-buy' : 'Run to the shop.',
		'go-fast to inside place-buy' : 'Run into the shop.',
		'go-fast to outside place-buy' : 'Run out of the shop.',
		'go-fast to backside place-buy' : 'Run [to] behind the shop.',
		'GIANT-do_voice from building' : 'Shout from the house.',
		'GIANT-do_voice from backside building' : 'Shout from behind the house.',
	}" />

</div>

<div v-else-if="cur_lesson == 'prefixes'">
	<div :class="$css.title"> Prefixes </div>

	<p> Prefixes are little modifiers that are placed right in the beginning of the word. </p>

	<p class="text-h5 text-center"> <nao n="GIANT"/> </p>

	<p> Similar to <i>very/big</i>, but we actually have a transition from quantity to quality.  </p>

	<PAT :little_columns="[
		{'good': 'good', 'GIANT-good': 'great'},
		{'convinced': 'believe', 'GIANT-convinced': 'be sure'},
		{'PASSIVE-find_funny': 'funny', 'GIANT-PASSIVE-find_funny': 'hilarious'},
		{'angry': 'be angry', 'GIANT-angry': 'be in a rage'},
	]" />

	<p class="text-h5 text-center"> <nao n="LITTLE"/> </p>

	<p> Similar to <i>a little</i>, but again we have a transition from quantity to quality.  </p>

	<PAT :little_columns="[
		{'female': 'female', 'LITTLE-female': 'girl'},
		{'cut': 'cut', 'LITTLE-cut': 'scratch'},
		{'sleep': 'sleep', 'LITTLE-sleep': 'nap'},
		{'convinced': 'believe', 'LITTLE-convinced': 'suppose'},
	]" />

	<p class="text-h5 text-center"> <nao n="o_great"/> </p>

	<p> This prefix can make any word sound poetic, of high style. </p>

	<PAT :little_columns="[
		{'good': 'good', 'o_great-good': 'wonderful'},
		{'want': 'want', 'o_great-want': 'desire'},
		{'think': 'think', 'o_great-think': 'contemplate'},
	]" />

	<p class="text-h5 text-center"> <nao n="ANGRY"/> </p>

	<p> Using this powerful prefix, you can turn any word into a swear word. </p>

	<PAT :little_columns="[
		{'bad': 'bad', 'ANGRY-bad': 'sh*t'},
		{'because-what': 'why', 'ANGRY-because-what': 'why the f*ck'},
		{'not-feel-important' : 'be indifferent', 'ANGRY-not-feel-important' : 'not give a f*ck'},
	]" />

	<p class="text-h5 text-center"> <nao n="INFORMAL"/> </p>

	<p> Another mighty prefix that makes any word sound informal, something used with friends and family. </p>

	<PAT :little_columns="[
		{'true': 'yes', 'INFORMAL-true': 'yep'},
		{'male': 'male', 'INFORMAL-male': 'dude'},
		{'relaxed': 'be relaxed', 'INFORMAL-relaxed': 'chill'},
	]" />
</div>

<div v-else-if="cur_lesson == 'prefix-like-words'">
	<div :class="$css.title"> Prefix-like words </div>

	<p> There are a few words that behave like prefixes – they are put right before the word they describe. Let’s try to understand how they work on the example of <nao o="sol"/> = only:</p>

	<PAT :line="!$vuetify.breakpoint.xs" :pairs="{
		'only me eat/drink potato' : 'Only I eat potatoes (I\'m the only one who eats them)',
		'me only eat/drink potato' : 'I only eat potatoes (I don\'t do anything else)',
		'me eat/drink only potato' : 'I eat only potatoes (I don\'t eat anything else)',
	}" />

	<p> As you can see, the position of <nao n="only"/> changes the meaning of the sentence. </p>

	<p> Now let's consider the word <b>nui</b> = also/too: </p>

	<PAT :line="!$vuetify.breakpoint.xs" :pairs="{
		'also male-that go \'tokio' : 'He, too, will go to Tokyo (among other people)',
		'male-that also go \'tokio' : 'Also he will go to Tokyo (among doing other things)',
		'male-that go also \'tokio' : 'He will go to Tokyo, too (among other places)',
	}" />

	<p> The word <b>bis</b> is used to put emphasis/stress, similar to writing a word in bold or caps. </p>

	<PAT :line="!$vuetify.breakpoint.xs" :pairs="{
		'EMPHASIS \'jon in_love \'sara' : 'it\'s John who is in love with Sara',
		'\'jon EMPHASIS in_love \'sara' : 'John is IN LOVE with Sara (not hate)',
		'\'jon in_love EMPHASIS \'sara' : 'it\'s Sara who John is in love with',
	}" />

	<p> Finally, take a look at <b>dac</b> = even: </p>

	<PAT :line="!$vuetify.breakpoint.xs" :pairs="{
		'even \'maik see/look cat' : 'Even Mike saw the cat. (among other people)',
		'\'maik even see/look cat' : 'Mike even saw the cat. (not just heard about it)',
		'\'maik see/look even cat' : 'Mike saw even the cat (among other pets)',
	}" />

	<p> So to conclude, you always have to be careful where you put prefix-like words since it affects the meaning of the sentence. </p>
</div>

<div v-else-if="cur_lesson == 'senses'">
	<div :class="$css.title"> Senses </div>

	<p> First, let's take a look at the five general senses:</p>

	<PAT line :pairs="{
		'see/look' : 'see/look/watch',
		'hear/listen' : 'hear/listen',
		'smell' : 'smell',
		'taste' : 'taste',
		'in_contact-physical' : 'touch'
	}" />

	<p> Is it possible to distinguish between <i>see</i> and <i>look</i>? Yes, depending on what kind of distinction you want. First of all, we have <nao n="notice"/> = notice. We can use it in combination with all of the above:</p>

	<PAT line :pairs="{
		'see/look-and_as_a_result-notice' : 'see',
		'hear/listen-and_as_a_result-notice' : 'hear',
		'smell-and_as_a_result-notice' : 'smell (the presence of)',
		'taste-and_as_a_result-notice' : 'taste (the presence of)',
		'in_contact-physical-and_as_a_result-notice' : 'feel the touch'
	}" />

	<p> All of the above mean your brain registers the presence of something, it happens indeliberately/subconsciously.</p>

	<p> On the other hand, we have the word <nao n="concentrated"/> = be concentrated: </p>

	<PAT line :pairs="{
		'concentrated-see/look' : 'look/watch',
		'concentrated-hear/listen' : 'listen',
		'concentrated-smell' : 'sniff',
		'concentrated-taste' : 'savor',
		'concentrated-in_contact-physical' : 'touch (actively)'
	}" />

	<p> All of the above mean the action is deliberate/conscious, you are concentrated.</p>

</div>

<div v-else-if="cur_lesson == 'transliteration'">
	<div :class="$css.title"> Transliteration </div>
	<p> When writing foreign words (e.g. proper nouns), you must change (adapt) their spelling to Nao according to the <router-link to="/transliterations"> official transliteration rules</router-link>. </p>

	<p> Why do we need to do that? Imagine someone sends you a message in Nao:</p>

	<div class="pl-5 pb-5"> <v-icon color="red"> {{ $mdi.Close }} </v-icon> joimit – me nom Krzysztof Wojciechowski </div>

	<p> If you don’t know the original language (in this case – Polish), you would definitely be confused by that name; you would have no idea how to pronounce that. That would break one of the basic rules of Nao: everything is pronounced exactly the way it is written. That’s why it is absolutely unacceptable to write foreign words in their original spelling. Instead, that guy should transliterate his name according to the <router-link to="/transliterations/Polish"> official transliteration rules for Polish</router-link> and rewrite his message like this: </p>

	<div class="pl-5 pb-5"> <v-icon color="green"> {{ $mdi.Check }} </v-icon> joimit – me nom 'kcyctof 'voitcehovski </div>

	<p> Now you know exactly how to pronounce that – letter by letter, just like any other Nao word. </p>

	<p> This also concerns English words − if you live in Ohio, you cannot write: </p>

	<div class="pl-5 pb-5"> <v-icon color="red"> {{ $mdi.Close }} </v-icon> me pas 'ohio </div>

	<p> Because that would be pronounced <i>OH-HEE-OH</i>. That's why you should refer to the <router-link to="/transliterations/English"> official transliteration rules for English</router-link> and rewrite your sentence: </p>

	<div class="pl-5 pb-5"> <v-icon color="green"> {{ $mdi.Check }} </v-icon> me pas 'ohaio </div>

	<p> It means that you always have to refer to the original language. For example, imagine you want to write <i>Warsaw</i> – but you must immediately realize that in Nao it’s definitely not spelled like that because Nao doesn’t even have the letter <i>w</i>. You need to find out the original spelling of the word, in this case <i>Warszawa</i>, and transliterate it according to the rules (automatically, if available) - in this case, to <b>'varcava</b>. </p>

	<p> Also note that the official transliteration doesn’t aim to perfectly reflect the pronunciation of words – after all, the pronunciation might differ from dialect to dialect. Instead, transliteration is a compromise between the average pronunciation, the phonemes and the spelling. Thanks to the official transliteration rules, there is always exactly one way to write any foreign word and one way to pronounce it using familiar native sounds. </p>
</div>

<div v-else-if="cur_lesson == 'numbers'">
	<div :class="$css.title"> Numbers </div>

	<p> There are ten basic digits you need to remember: </p>

	<PAT :little_columns="[
		{'0': 'nol', '1': 'ji', '2': 'dua', '3':'tui', '4':'fos'},
		{'5': 'pet', '6': 'lok', '7': 'set', '8':'hap', '9':'nef'},
	]" />

	<p> Then, in order to say any number, you can simply read out its digits (and <nao o="dot"/>): </p>

	<PAT :little_columns="[
		{'10': 'jinol', '398': 'tuinefhap', '2017': 'duanoljiset', '3.14':'tuidotjifos'},
	]" />

	<p> To express really big numbers, we can use <b>pu</b> + number of zeroes: </p>

	<PAT :little_columns="[{
		'GIANT-three' : '1,000',
		'eight GIANT-three' : '8,000',
		'GIANT-six' : '1,000,000',
		'one-two GIANT-six' : '12,000,000',
	}]" />

	<p> To express really small numbers, we can use <b>pi</b> + number of zeroes: </p>

	<PAT :little_columns="[{
		'LITTLE-three' : '0.001',
		'four LITTLE-three' : '0.004',
		'LITTLE-six' : '0.000001',
		'nine-four LITTLE-six' : '0.000094',
	}]" />

	<p> To express quantity, numbers are put in front of the noun: </p>

	<PAT :pairs="{
		'five dog' : 'five dogs',
		'eight GIANT-three money-USA' : 'eight thousand US dollars',
	}" />

	<p> Ordinal numbers are created by using <b>hai</b> (= to be in order): </p>

	<PAT :pairs="{
		'organized-one' : 'first',
		'organized-four-two' : '42nd',
	}" />

	<p> Numbers are very useful in Nao. They are used to express different words of time. Such as names for days of the week: </p>

	<PAT line :pairs="{
		'weekday' : 'weekday',
		'weekday-one' : 'Monday',
		'weekday-two' : 'Tuesday',
		'weekday-seven' : 'Sunday',
	}" />

	<p> Also names for months: </p>

	<PAT line :pairs="{
		'element-year' : 'month',
		'element-year-one' : 'January',
		'element-year-two' : 'February',
		'element-year-one-two' : 'December',
	}" />

	<p> Here is an example of a date: </p>

	<PAT no_pronounce :pairs="{
		'hour-19 minute-45 day-24 element-year-12 year-2020' : '19:45, 24 December 2020',
	}" />

	<p> As you can see, you can write numbers in words or in digits - it's correct either way. Note that the number is always at the end, it is important. </p>
</div>


<div v-else-if="cur_lesson == 'family-members'">
	<div :class="$css.title"> Family members </div>

	<p> The poweful word construction ability of Nao allows you to create words for all blood-related family members using only three basic words: </p>

	<PAT line :pairs="{
		'parent' : 'parent',
		'male' : 'male',
		'sibling' : 'sibling',
	}" />

	<p> From two of them we create the opposite words: </p>

	<PAT line :pairs="{
		'offspring' : 'child',
		'female' : 'female',
	}" />

	<p> At first we make easy words: </p>

	<PAT :pairs="{
		'female-parent' : 'mother',
		'male-parent' : 'father',
		'female-offspring' : 'daughter',
		'male-offspring' : 'son',
		'female-sibling' : 'sister',
		'male-sibling' : 'brother',
	}" />

	<p> Then, if we say <nao n="parent-parent"/>, it means <i>parent of parent</i>, i.e. <i>grandparent</i>: </p>

	<PAT :pairs="{
		'female-parent-parent' : 'grandmother',
		'male-parent-parent' : 'grandfather',
		'female-offspring-offspring' : 'granddaughter',
		'male-offspring-offspring' : 'grandson',
	}" />

	<p> We can also be more precise by specifying the gender on each <b>pau/pai</b>: </p>

	<PAT :pairs="{
		'female-parent-female-parent' : 'mother\'s mother',
		'male-offspring-female-offspring' : 'daughter\'s son',
	}" />

	<p> Repeating <nao n="parent"/> three times will give us <i>great-grandparent</i>, etc. To avoid repetition, you can use a number: </p>

	<PAT :pairs="{
		'parent-three' : 'great-grandparent',
		'parent-five' : 'great-great-great-grandparent',
	}" />

	<p> You can also use numbers to refer to cousins: </p>

	<PAT :pairs="{
		'sibling-two' : 'first cousin (share the same grandparents)',
		'sibling-three' : 'second cousin (share the same great-grandparents)',
	}" />

	<p> Going further: </p>

	<PAT :pairs="{
		'female-sibling-parent' : 'aunt (parent\'s sister) ',
		'male-sibling-parent' : 'uncle (parent\'s brother)',
		'female-offspring-sibling' : 'niece (sibling\'s daughter)',
		'male-offspring-sibling' : 'nephew (sibling\'s son)',
		'female-sibling-parent-parent' : 'grandaunt (grandparent\'s sister)',
		'male-sibling-parent-parent' : 'granduncle (grandparent\'s brother)',
		'female-offspring-offspring-sibling' : 'grandniece (sibling\'s granddaughter)',
		'male-offspring-offspring-sibling' : 'grandnephew (sibling\'s grandson)',
	}" />

	<p> Then we can use <nao show_tr n="married"/> to make marriage-related family members: </p>

	<PAT :pairs="{
		'person-married' : 'spouse',
		'female-married' : 'wife',
		'male-married' : 'husband',
	}" />

	<p> Using that, we can easily refer to in-laws: </p>

	<PAT :pairs="{
		'female-parent-person-married' : 'mother-in-law',
		'female-married-offspring' : 'daughter-in-law',
	}" />

	<p> Note that in some cases, English is ambiguous while Nao is precise: </p>

	<PAT :pairs="{
		'male-sibling-person-married' : 'brother-in-law (spouse\'s brother)',
		'male-married-sibling' : 'brother-in-law (sibling\'s husband)',
		'male-sibling-parent-person-married' : 'uncle-in-law (spouse\'s uncle)',
		'male-married-sibling-parent' : 'uncle-in-law (aunt\'s husband)',
	}" />

	<p> Finally, use <b>tia</b> to make words for stepfamily: </p>

	<PAT :pairs="{
		'female-parent-side' : 'stepmother',
		'male-parent-side' : 'stepfather',
		'female-offspring-side' : 'stepdaughter',
		'male-offspring-side' : 'stepson',
		'female-sibling-side' : 'stepsister',
		'male-sibling-side' : 'stepbrother',
	}" />

	<p> Again, some English words are ambiguous while Nao differentiates between all by placing <b>tia</b> in different positions: </p>

	<PAT :pairs="{
		'female-parent-parent-side' : 'stepgrandmother (stepparent\'s mother)',
		'female-parent-side-parent' : 'stepgrandmother (parent\'s stepmother)',
		'paikiopautia' : 'stepcousin (stepparent\'s nephew/niece)',
		'paikiotiapau' : 'stepcousin (parent\'s stepsibling\'s child)',
		'paitiakiopau' : 'stepcousin (stepchild of one\'s aunt or uncle.)',
	}" />

</div>

<div v-else-if="cur_lesson == 'colors'">
	<div :class="$css.title"> Colors </div>
	<p> The color system in Nao is very powerful – it allows you to express any possible color. It is based on HSL (hue-saturation-luminosity) model. Look at the color wheel below and you can notice six basic colors (like the seven colors of the rainbow but orange being an intermediate color). Try to click on the colorwheel and you'll understand how it works. </p>

	<div class="d-flex justify-center pb-5">
		<Colorwheel :size="cw_size" />
	</div>

	<p> So how does it work? First we have to memorize the 6 basic colors: </p>

	<PAT line no_pronounce :pairs="{
		'color-0 (color-zero)' : 'red',
		'color-1 (color-one)'  : 'yellow',
		'color-2 (color-two)' : 'green',
		'color-3 (color-three)' : 'cyan',
		'color-4 (color-four)' : 'indigo',
		'color-5 (color-five)' : 'magenta',
	}" />

	<p> Note that for shortness, we can drop final zeroes: </p>
	
	<p class="pl-8"> <nao n="color-10 = color-1" />  </p>

	<p> Then we can easily get intermediate colors. For example, between <nao n="color-0-0"/> and <nao n="color-1-0"/>: </p>

	<PAT :pairs="{
		'color-05' : 'orange',
		'color-08' : 'gold',
	}" />

	<p> Then we can apply the words <nao n="light"/> (light) or <nao n="dark"/> (dark) to create more colors: </p>

	<PAT :pairs="{
		'color-05 dark' : '(dark orange) = brown',
		'color-5 light' : '(light magenta) = pink',
	}" />

	<p> Then we can also use the word <nao n="weak"/> (weak) to lower the saturation (mix with grey): </p>

	<PAT :pairs="{
		'color-zero-weak' : 'greyish red',
	}" />

	<p>There are three special colors:</p>

	<PAT line :pairs="{
		'color-completely-light' : '(completely light color) = white',
		'color-completely-dark' : '(completely dark color) = black',
		'color-completely-weak' : '(completely weak color) = grey',
	}" />

	<p> You can also enter any color in the dictionary and it will show it to you. <router-link to="/dict/hanolpetlau_hagalai_ha35_lai7"> Try it! </router-link> </p>
</div>

<div v-else-if="cur_lesson == 'verbal-emoji'">
	<div :class="$css.title"> Verbal emoji </div>

	<p> As you already know, Nao doesn’t use any sentence finals like “!”, “?”, etc. Fortunately, Nao has something that is much more powerful and unambiguous – verbal emoji: </p>

	<PAT no_pronounce :pairs="{
		'you do what =find_funny=' : 'What are you doing? LOL',
	}" />

	<p> As you can see, <nao n="=find_funny="/> is a verbal emoji and is roughly equivalent to <i>LOL</i>. So to create a verbal emoji, you need to take a key word that describes your attitude to what is being said and use it in the beginning or at the end of the sentence (replacing the dash if any). </p>

	<PAT no_pronounce :pairs="{
		'me not FUTURE go that =feel-in_danger=' : 'I’m not going there (scared)',
		'PLURAL-me buy vehicle_with_cabin-solid =happy=' : 'We bought a car (happy)',
		'person-some go-and_as_a_result-be_located =important=' : 'Somebody has arrived (important)',
		'because-what do that =GIANT-feel-extraordinary=' : 'Why did you do that?! (extremely surprised)',
		'you work what =want-know=':'What is your job (curious)',
		'act_of-live short quantity-big =taldofam=': 'Life is very short... (worth thinking about)'
	}" />

	<p> Verbal emoji are also recommended to use in place of interjections, which can differ dramatically from language to language and can easily lead to misunderstandings. It can be done via usage of <nao n="do_voice"/>.  </p>

	<PAT no_pronounce :pairs="{
		'=do_voice-happy=' : 'Yay',
		'=do_voice-succeed-understand=' : 'Ah (when you have succeeded in understanding something)',
		'=do_voice-feel-extraordinary=' : 'Woah/wow',
		'=do_voice-feel-dirty=' : 'Ew',
		'=do_voice-find_funny=' : 'Haha',
		'=do_voice-think=' : 'Hmmm',
		'=do_voice-understand=' : 'Aha, mhm (when you understand)',
		'=do_voice-PAST-LITTLE-not-convinced-FUTURE=' : 'Oh (when you heard something a bit unexpected)',
		'=do_voice-feel-in_danger=' : 'AAAH!! (screaming out of fear)',
		'=do_voice-sad=' : 'Uhuhu (crying)',
		'=do_voice-feel-cute=' : 'Awwww (when seeing smth cute)',
	}" />

	<p> As you can see, this trick is very powerful, it allows you to express any possible interjection, convey any meaning. </p>
</div>

<div v-else-if="cur_lesson == 'passive'">
	<div :class="$css.title"> Passive </div>

	<p> Simiarly with English, passive voice in Nao is used to say an action is done to someone/something. To put any verb into the passive voice form, just add <nao o="do"/> in the beginning. </p>

	<PAT :pairs="{
		'cat eat/drink mouse' : 'The cat eats the mouse.',
		'mouse PASSIVE-eat/drink by cat' : 'The mouse is eaten by the cat.',
	}" />

	<p> You can notice that we can specify the agent of the action with the special preposition <nao o="doc"/>. </p>

	<p> However in Nao, the passive voice has a much broader usage than in English. It's an instrument to put the object in the first place and make it the center of attention. First, take a look: </p>

	<PAT :pairs="{
		'male-that like see/look act_of-on_fire' : 'He likes to look at the fire.',
	}" />

	<p> Now let's reverse the sentence, putting the fire in front: </p>

	<PAT :pairs="{
		'act_of-on_fire PASSIVE like see/look by male-that' : 'Fire is beautiful to him.',
	}" />

	<p> There we are talking about a specific person and his preferences. But what if a lot of people enjoy looking at the fire? Then we can consider that as an attribute of fire, specifically its beauty: </p>

	<PAT :pairs="{
		'act_of-on_fire PASSIVE like see/look' : 'Fire is beautiful.',
	}" />

	<p> Removing the spaces, we end up with the word <nao n="PASSIVE-like-see/look"/> = beautiful. </p>

	<p> Here's another example, along with <nao n="cause"/>: </p>

	<PAT :pairs="{
		'female-that feel-in_danger dog' : 'She is scared of the dogs.',
		'dog PASSIVE-feel-in_danger' : 'Dogs are scary.',
		'dog cause-feel-in_danger male-that' : 'A dog scared him.',
	}" />

	<p> Here are similar word groups:</p>

	<PAT :little_columns="[
		{'bored': 'be bored', 'PASSIVE-bored': 'be boring', 'cause-bored' : 'make bored' },
		{'feel-extraordinary': 'be surprised', 'PASSIVE-feel-extraordinary': 'be surprising', 'cause-feel-extraordinary' : '(v) surprise' },
		{'find_funny': 'find (smth) funny', 'PASSIVE-find_funny': 'be funny', 'cause-find_funny' : 'make laugh' },
	]" />

</div>


<div v-else-if="cur_lesson == 'common-confusions'">
	<div :class="$css.title"> Common confusions </div>

	<p class="text-h5 text-center"> <nao n="see/look"/> vs <nao n="notice"/> </p>

	<p> Both of them might be translated as <i>see</i>, but what’s the difference? </p>

	<p>
		<nao n="see/look"/> is about getting visual information with your eyes <br>
		<nao n="notice"/> is about noticing, not necessarily with your eyes. It means registering a certain action but not necessarily concentrating on it
	</p> 

	<PAT :pairs="{
		'me PAST notice person-that eat/drink' : 'I saw (or heard, or smelled) that person eat (meaning there was a chance I would\'ve missed on that)',
		'me PAST see/look person-that eat/drink' : 'I saw (or watched) that person eat (no information about concentration level)',
	}" />

	<p> For more information, read the lesson <router-link to="/textbook/senses">Senses</router-link>. </p>

	<p class="text-h5 text-center"> <nao n="think"/> vs <nao n="convinced"/> </p>

	<p>
		<nao n="convinced"/> is about having an opinion, a belief <br>
		<nao n="think"/> is about the thinking process, for example when you're trying to solve a problem or just imagining something.
	</p>

	<PAT :pairs="{
		'me convinced female-that commit_crime' : 'I think that she committed a crime',
		'me think female-that commit_crime' : 'I imagine her commiting a crime',
		'you convinced what' : 'What do you think? (What is your opinion?)',
		'you think what' : 'What are you thinking about?',
	}" />

	<p class="text-h5 text-center"> <nao n="for_the_sake"/> </p>

	<p> Some people mistakenly think that <nao n="for_the_sake"/> is the same as the English preposition <i>for</i>. In reality, <i>for</i> has so many meanings while <nao n="for_the_sake"/> = <i>for the sake of</i> or <i>in order to</i>. </p>

	<PAT :pairs="{
		'me PAST-do that for_the_sake you' : 'I did that for you (for your sake)',
		'male-that begin-know-with-understand language-Polish for_the_sake go country-Polish' : 'He is learning Polish in order to go to Poland.',
	}" />

	<p class="text-h5 text-center"> Apostrophe </p>

	<p> Sometimes people take foreign words and just stick an apostrophe to it, for example:</p>

	<div class="pl-5 pb-5"> <v-icon color="red"> {{ $mdi.Close }} </v-icon> muta jo 'playstation </div>

	<p>However, that is wrong. The function of the apostrophe is to let people know that it is an adopted Nao word which can be pronounced exactly as it is spelled. So you either have to transliterate that word or write it in quotes: </p>

	<div class="pl-5 pb-5"> <v-icon color="green"> {{ $mdi.Check }} </v-icon> muta jo 'pleisteicyn </div>
	<div class="pl-5 pb-5"> <v-icon color="green"> {{ $mdi.Check }}</v-icon> muta jo "playstation" </div>

	<p class="text-h5 text-center"> <nao n="place"/> vs <nao n="object-be_located"/> </p>

	<p> <nao n="object-be_located"/> means <i>place</i> in general, it’s a simple word. However, <nao n="place"/> is much more specific, it means a place with a certain quality or function. So it is recommended against using <b>ma</b> unless you are really sure what you're doing. </p>

	<p class="text-h5 text-center"> <nao n="ADVERB-true"/> vs <nao n="quantity-feel-extraordinary"/> </p>

	<p> Both words can be translated as <i>really</i>, but they are very different: </p>

	<p>
		<nao n="ADVERB-true"/> is used to eliminate doubt<br>
		<nao n="quantity-feel-extraordinary"/> is interchangeable with <i>very</i>
	</p>

	<PAT :pairs="{
		'QUESTION you ADVERB-true person-go-space' : 'Are you really an astronaut?',
		'language-Japanese PASSIVE-find_difficult quantity-feel-extraordinary' : 'Japanese is really (=very) difficult.',
	}" />

	<p class="text-h5 text-center">
		<nao n="type-PASSIVE-allowed"/> vs
		<nao n="true"/> vs
		<nao n="speak-someone/something-true"/> vs
		<nao n="convinced-someone/something-true"/>
	</p>

	<p> All words can be translated as <i>right</i>, therefore they’re often mixed up by many learners. </p>

	<p>
		<nao n="true"/> means <i>true/real</i>; most of the time it’s applied to clauses<br>
		<nao n="type-PASSIVE-allowed"/> means <i>correct</i>; but keep in mind it’s not applicable to people<br>
		<nao n="speak-someone/something-true"/> means <i>to say something true</i>; that’s exactly what we apply to people<br>
		<nao n="convinced-someone/something-true"/> means <i>to believe something that is true</i>
	</p>

	<PAT :pairs="{
		'male-that person-cause-healthy true' : 'He is a true/real doctor',
		'word that type-PASSIVE-allowed' : 'That word is correct',
		'male-that speak-someone/something-true' : 'He is right (he said something true)',
		'male-that convinced-someone/something-true' : 'He is right (his opinion is correct)',
	}" />

	<p> Here's what happens if you misuse them: </p>

	<PAT :pairs="{
		'male-that true' : 'He is real (something we might hear if we meet a famous actor and we can’t believe it’s really him)',
	}" />

	<PAT :pairs="{
		'male-that type-PASSIVE-allowed' : '??? (A really strange phrase that would mean as if there are correct and incorrect types of people)'
	}" />

	<p> And of course the same concerns their opposites:
		<nao n="type-PASSIVE-forbidden"/>, 
		<nao n="false"/>, 
		<nao n="speak-someone/something-false"/>, 
		<nao n="convinced-someone/something-false"/>.
	</p>
</div>

<div v-else-if="cur_lesson == 'markers'">
	<div :class="$css.title"> Markers </div>

	<p> Markers are little grammatical words that can help make the structure of the sentence more clear. They are usually used in either long complicated sentences, or in case of a non-standard word order. Most of them are placed in front of what they mark. The space after the marker is optional. </p>

	<p class="text-h5 text-center"> Marker <nao n="SUBJECT/MAIN_VERB"/> </p>

	<p> It can mark the subject and/or the main verb of the sentence. The two cannot be confused with each other since the subject is always a noun whereas the main verb is always a verb. </p>

	<PAT :pairs="{
		'person-cause-healthy (SUBJECT/MAIN_VERB) work' : 'The doctor is working.',
		'work SUBJECT/MAIN_VERB person-cause-healthy' : 'The doctor is working.',
	}" />

	<p> We could also theoretically put <nao n="SUBJECT/MAIN_VERB"/> in the beginning of those sentences too, but it would be pointless since by default the sentence always begins with the subject or the main verb. </p>

	<p class="text-h5 text-center"> Marker <nao n="OBJECT"/> </p>

	<p> Put it in front of the main object of the sentence. </p>
	<PAT :pairs="{
		'ask-cause-notice to person-cause-healthy OBJECT bodypart-bite you' : 'Show the doctor your teeth.',
	}" />

	<p> Note if we remove the marker from the sentence above, the meaning will change because <nao n="bodypart-bite"/> will form a bond with <nao n="person-cause-healthy"/> producing <i>dentist</i>: </p>

	<PAT :pairs="{
		'ask-cause-notice to person-cause-healthy bodypart-bite you' : 'Show your dentist [something]',
	}" />

	<p> Due to the associativity property, there are often multiple ways to determine the main object: </p>

	<PAT :pairs="{
		'me OBJECT see/look you SUBJECT/MAIN_VERB want' : 'I want to see you.',
		'me OBJECT you SUBJECT/MAIN_VERB want see/look' : 'I want to see you.',
	}" />

	<p> We can use those two markers to form all six possible word orders. All the sentences below mean <i> I look at an apple </i>: </p>

	<PAT :pairs="{
		'me SUBJECT/MAIN_VERB see/look apple' : 'S V O',
		'me OBJECT apple SUBJECT/MAIN_VERB see/look' : 'S O V',
		'see/look SUBJECT/MAIN_VERB me OBJECT apple' : 'V S O',
		'see/look OBJECT apple SUBJECT/MAIN_VERB me' : 'V O S',
		'OBJECT apple SUBJECT/MAIN_VERB me see/look' : 'O S V',
		'OBJECT apple SUBJECT/MAIN_VERB see/look SUBJECT/MAIN_VERB me' : 'O V S',
	}" />

	<p class="text-h5 text-center"> Marker <nao n="of"/> </p>

	<p> It is the marker of a noun modifier so it is often referred to as the adjective marker. You can apply it to verbs: </p>

	<PAT :pairs="{
		'person-in_contact-good of high' : 'A tall friend',
		'person-cause-healthy of do_voice-sad' : 'A crying doctor',
		'person-like of PAST-cause-more-find_easy' : 'Fans who helped',
	}" />

	<p> You can also apply it to nouns: </p>

	<PAT :pairs="{
		'container-cold of me' : 'my fridge',
		'object-sit of substance-tree' : 'wooden chair',
		'object-need-solve of act_of-know-with-understand-object-quantity' : 'A mathematical problem',
	}" />

	<p> Note that the marker does not carry any meaning on its own. It does not specifically mean ownership or the material or anything like that - its sole purpose is to show the connection between words. </p>

	<p class="text-h5 text-center"> Marker <nao n="ADVERB"/> </p>

	<p> It marks the main adverb of the sentence. </p>

	<PAT :pairs="{
		'person-that speak ADVERB-convinced-self-able' : 'He/she speaks confidently.',
		'ADVERB-be parent, female-that able understand object-in_state that' : 'Being a parent, she could understand that situation.'
	}" />

	<p> By default, the time of the adverb is the same as the time of the verb. But we can modify it: </p>

	<PAT :pairs="{
		'ADVERB-PAST-go-inside room, PLURAL-person-that begin-notice some' : 'Having entered the room, they noticed something.',
	}" />
</div>

<div v-else-if="cur_lesson == 'connectors'">
	<div :class="$css.title"> Connectors </div>
	<p> There are three pairs of words called connectors because they most often connect two sentences. </p>
	<PAT :pairs="{
		'A because B' : 'A because (of) B',
		'B therefore A' : 'B therefore A',
		'A if B' : 'A if B',
		'B then A' : 'if B then A',
		'A although B' : 'A even though B',
		'B but A' : 'B but A',
	}" />

	<p> As you can see, they come in pairs because we just the swap <i>A</i> and <i>B</i>, and the meaning stays the same. <i>A</i> is the result and <i>B</i> is the original condition. Here is an example of the least obvious one - the two sentences below have the same meaning: </p>

	<PAT :pairs="{
		'me eat/drink that although not feel-need-eat/drink' : 'I ate it even though I wasn\'t hungry.',
		'not feel-need-eat/drink but me eat/drink that' : 'I wasn\'t hungry but I ate it.',
	}" />
</div>

<div v-else-if="cur_lesson == 'associativity'">
	<div :class="$css.title"> Associativity </div>

	<p> Associativity is one of the main properties of Nao. Firsly, it implies little importance of spaces. Consider the English word <i>raincoat</i>. We might as well write it as <i>rain coat</i> and the meaning wouldn’t change. So is <i>raincoat</i> one word or two words? It depends on whether we write a space or not. Nao grammar works exactly that way but on a much larger scale. Let’s take a look at some examples: </p>

	<p> <nao n="ask-not-stop-try"/> can be disassembled in these ways: </p>

	<PAT line :pairs="{
		'ask-not stop-try': 'don\'t give up',
		'ask not-stop try': 'please continue trying'
	}" />

	<p> Either way, the meaning is the same, which means that it doesn’t matter where you put the spaces. </p>

	<PAT line :pairs="{
		'go-outside building': 'exit a/the building',
		'go outside-building': 'go outdoors'
	}" />

	<PAT line :pairs="{
		'male-parent-parent me': 'my grandfather',
		'male-parent parent-me': 'my parent\'s father',
	}" />

	<PAT line :pairs="{
		'person-cause-healthy-bodypart-bite you': 'your dentist',
		'person-cause-healthy bodypart-bite you': 'doctor of your teeth',
	}" />

	<PAT line :pairs="{
		'eight GIANT-three-meter': '8 kilometers',
		'eight-GIANT-three meter': '8000 meters',
	}" />

	<p> All of the above could even be written with no spaces at all. </p>

	<p> Associativity also lets us change the meaning to the opposite without knowing the structure of the word: </p>

	<PAT line :pairs="{
		'person-prone-convinced-FUTURE-good': 'optimist',
		'person-prone-convinced-FUTURE-bad': 'pessimist',
	}" />

	<p> However, it doesn't mean that spaces don't matter at all in the whole language. Spaces still play a certain role in separating the words in sentences ensuring the correct grammar bonds. Although if we speak with a heavy use of markers then it is possible to do without spaces. </p>
</div>

<div v-else-if="cur_lesson == 'logical-operators'">
	<div :class="$css.title"> Logical operators </div>

	<p> In this lesson we will consider the words <nao n="all"/>/<nao n="none"/>, <nao n="and"/>/<nao n="or"/>, <nao n="not"/> and how they interact with each other. </p>

	<p class="text-h5 text-center"> <nao n="all"/>/<nao n="none"/> </p>

	<p> Normally when you see those in a sentence, you imagine splitting it into an array of sentences. We will use the square bracket notiation for that and make sure you understand it in the examples below because we will use it a lot in this lesson. </p>

	<PAT :pairs="{
		'person-all want that = [da1, da2, ...] want that': 'Everybody wants it',
	}" />

	<PAT :pairs="{
		'person-none (not) want that = false CLAUSE [da1, da2, ...] want that': 'Nobody wants it',
	}" />

	<p> </p>

	<p> As seen from the second example, Nao allows for double negation. Moreover, the meaning of the sentence can change depending on where the negation is placed: </p>

	<PAT :pairs="{
		'me (not) try cause-injured person-none': 'I didn\'t try to hurt anybody',
		'me try not cause-injured person-none': 'I tried not to hurt anybody',
		'female-that (not) begin have none' : 'She received nothing.',
		'female-that begin not have none' : 'She lost everything.'
	}" />

	<p> As shown above, if the negation word is left out, it is thought to be hidden before the main verb. </p>

	<p> Important rule: if the sentence includes the negation <nao n="not"/> in the same expression with <nao n="all"/>, we first factor out the negation: </p>

	<PAT :pairs="{
		'male-that not know all (= false CLAUSE male-that know all)': 'He doesn\'t know everything (= not true that he knows everything)',
	}" />

	<p class="text-h5 text-center"> <nao n="and"/>/<nao n="or"/> </p>

	<p> When we see one of those operators, we just split the sentence into two. It doesn't matter whether it includes a negation or not.</p>
	<PAT :pairs="{
		'male-that able language-English and language-Russian': '[He speaks English] and [He speaks Russian]',
		'male-that able language-English or language-Russian': '[He speaks English] or [He speaks Russian]',
		'male-that not able language-English and language-Russian': '[He doesn\'t speak English] and [He doesn\'t speak Russian]',
		'male-that not able language-English or language-Russian': '[He doesn\'t speak English] or [He doesn\'t speak Russian]',
	}" />

	<p> Finally, when we see both of them in the sentence, we first split the sentence into the array of sentences: </p>

	<PAT :pairs="{
		'person-all able language-English and language-Russian': '[person<sub>1</sub>, person<sub>2</sub>, ...] speaks English and Russian <br>(Everybody speaks both languages)',
		'person-all able language-English or language-Russian': '[person<sub>1</sub>, person<sub>2</sub>, ...] speaks English or Russian <br>(Everybody speaks at least one language)',
		'person-none (not) able language-English and language-Russian': 'Not true that [person<sub>1</sub>, person<sub>2</sub>, ...] speaks English and Russian <br>(Nobody speaks both languages)',
		'person-none (not) able language-English or language-Russian': 'Not true that [person<sub>1</sub>, person<sub>2</sub>, ...] speaks English or Russian <br>(Nobody speaks any of the languages)',
	}" />
</div>

<div v-else-if="cur_lesson == 'constructing-verbs' ">
	<div :class="$css.title"> Constructing new verbs </div>

	<p> In this lesson you will learn some little but powerful elementary words that help you build new verbs. In many other languages, various suffixes are used for that purpose. However, in Nao they are completely independent verbs. </p>

	<p class="text-h5 text-center"> <nao n="begin"/> = to begin </p>

	<p> The majority of verbs in Nao are states/processes. The verb <nao n="begin"/> is used to denote the beginning of the given state/process. </p>

	<PAT :pairs="{
		'begin-want': 'begin to want',
		'begin-cold': '(begin to be cold) = become cold',
		'begin-dead': '(begin to be dead) = die',
		'begin-know': '(begin to know) = find out/learn about',
		'begin-different': '(begin to be different) = change'
	}" />

	<p class="text-h5 text-center"> <nao n="stop"/> = to stop </p>

	<p> The opposite of the verb above. It is used in a similar fashion. It is often used to express <i>not anymore</i>: </p>

	<PAT :pairs="{
		'stop-want': 'stop wanting (not want anymore)',
		'stop-remember': '(stop remembering) = forget',
		'stop-on_fire': '(stop being on fire) = go out',
	}" />

	<p class="text-h5 text-center"> <nao n="cause"/> = to cause </p>

	<p> This little verb is the most common element in the dictionary. Its usefulness cannot be underestimated as it allows one to construct a countless number of concepts: </p>

	<PAT :pairs="{
		'cause-dead': '(cause to be dead) = to kill',
		'cause-on_fire': '(cause to be on fire) = to set on fire',
		'cause-know': '(cause to know) = tell/inform',
		'cause-safe': '(cause to be safe) = protect',
	}" />

	<p> Native English speakers should be alert because many English verbs are transitive and intransitive at the same time, but in Nao they will always be different: </p>

	<PAT :pairs="{
		'begin-different': 'change (become different)',
		'cause-different': 'change (make smth different)',
		'begin-open' : 'open (become open)',
		'cause-open' : 'open (cause to open)',
		'stop-go' : 'stop (stop moving)',
		'cause-stop-go' : 'stop (make smb stop moving)',
	}" />

	<p class="text-h5 text-center"> <nao n="prone"/> = to be prone </p>

	<p> It's used to point out people's character traits and behavioral patterns. </p>

	<PAT :pairs="{
		'prone-feel-in_danger': '(prone to be scared) = cowardly',
		'prone-know': '(prone to know) = knowledgeable',
		'prone-speak-quantity-big': '(prone to say a lot) = talkative',
		'prone-find_difficult-choose' : '(prone to find it difficult to decide) = indecisive',
	}" />

	<p class="text-h5 text-center"> <nao n="undo"/> = undo </p>

	<p> A useful word that works pretty much like Ctrl+Z for any action. </p>

	<PAT :pairs="{
		'undo-write': '(undo writing) = erase',
		'undo-eat/drink': '(undo eating/drinking) = vomit',
		'undo-cause-injured': '(undo damaging) = repair',
	}" />
</div>

<div v-else-if="cur_lesson == 'constructing-nouns' ">
	<div :class="$css.title"> Constructing new nouns </div>

	<p> It is a common question among beginners - how do I make nouns in Nao? It depends on what meaning you want. In this lesson we will introduce the most common elements for that purpose. </p>

	<p class="text-h5 text-center"> <nao n="person"/> = person </p>

	<p> A very simple noun that denotes a person that performs an action or possesses some quality. It often corresponds to the suffix <i>-er</i> in English: </p>

	<PAT :pairs="{
		'person-work': '(person who works) = worker/employee',
		'person-cause-healthy': '(person who cures) = doctor',
		'person-use': '(person who uses) = user',
		'person-see/look': '(person who sees/looks/watches) = viewer',
	}" />

	<p class="text-h5 text-center"> <nao n="tool"/> = tool </p>

	<PAT :pairs="{
		'tool-fight': '(tool + fight) = weapon',
		'tool-light': '(tool + light) = lamp',
		'tool-time': '(tool + time) = clock/watch',
		'tool-see/look-space': '(tool + look + space) = telescope',
	}" />

	<p class="text-h5 text-center"> <nao n="act_of"/> = act (of) </p>

	<p> This word does not actually modify the meaning of the verb in any way. It is more like a marker whose purpose is purely grammatical. </p>

	<PAT :pairs="{
		'act_of-begin-dead': '(act of + die) = death',
		'act_of-explode': '(act of + explode) = explosion',
		'act_of-warm/hot': '(act of + warm/hot) = (noun) heat',
		'act_of-fight': '(act of + fight) = (noun) fight',
		'act_of-live': '(act of + live) = life',
		'act_of-cook': '(act of + cook) = (noun) cooking',
	}" />

	<p class="text-h5 text-center"> <nao n="someone/something"/> = thing (subject) </p>

	<p> Used to refer to the subject of the verb: </p>

	<PAT :pairs="{
		'someone/something-cover': '(thing that covers) = (noun) cover',
		'someone/something-live': '(thing that lives) = living thing',
		'someone/something-small': '(thing that is small) = small thing',
	}" />

	<p class="text-h5 text-center"> <nao n="object"/> = thing (object) </p>

	<p> One of the most common elements in the language used to refer to the object of the verb. Effectively it is a shorthand for <nao n="someone/something-PASSIVE"/>. </p>

	<PAT :pairs="{
		'object-know': '(thing that is known) = knowledge',
		'object-sell': '(thing that is sold) = (noun) goods',
		'object-more-like': '(thing that is preferred) = preference(s)',
		'object-convinced': '(thing that is believed) = belief/opinion',
		'object-hear/listen': '(thing that is heard) = sound',
		'object-have': '(thing that is owned) = property',
	}" />

	<p class="text-h5 text-center"> <nao n="place"/> = place </p>

	<p> This word means a place where person/people can come, stay there. It’s always a place (usually public) with a certain characteristics or function. </p>

	<PAT :pairs="{
		'place-buy': '(place + buy) = shop',
		'place-cause-healthy': '(place + cure) = hospital',
		'place-eat/drink-alcohol': '(place + drink alcohol) = bar',
		'place-begin-know-with-understand': '(place + study) = school',
	}" />

	<p class="text-h5 text-center"> <nao n="bodypart"/> = bodypart </p>

	<p> All parts of the body are constructed using this element. All you need to do is to specify the main purpose/function of the given bodypart or organ. </p>

	<PAT :pairs="{
		'bodypart-see/look': '(bodypart + see/look) = eye(s)',
		'bodypart-go': '(bodypart + go) = leg(s)',
		'bodypart-push-blood': '(bodypart + push blood) = heart',
		'bodypart-think': '(bodypart + think) = brain',
	}" />


</div>

<div v-else-if="cur_lesson == 'quantity-and-degree' ">
	<div :class="$css.title"> Quantity and degree </div>

	<p> To show quantity/amount for nouns and different degrees for adjectives and verbs, we can use the special word <nao n="quantity"/> with any word describing the amount/quantity/degree: </p>

	<p class="text-h5 text-center"> <nao n="quantity-big"/> (big quantity) </p>

	<PAT :pairs="{
		'sleep quantity-big': 'sleep a lot',
		'far quantity-big': 'very far',
		'person quantity-big': 'a lot of people',
	}" />

	<p class="text-h5 text-center"> <nao n="quantity-what"/> (what quantity) </p>

	<PAT :pairs="{
		'sleep quantity-what': 'sleep how much',
		'far quantity-what': 'how far',
		'person quantity-what': 'how many people',
	}" />

	<p class="text-h5 text-center"> <nao n="quantity-some"/> (some quantity) </p>

	<PAT :pairs="{
		'sleep quantity-some': 'sleep a bit',
		'far quantity-some': 'a bit far',
		'person quantity-some': 'some number of people',
	}" />

	<p class="text-h5 text-center"> <nao n="quantity-feel-extraordinary"/> (surprising quantity) </p>

	<PAT :pairs="{
		'sleep quantity-feel-extraordinary': 'sleep so much',
		'far quantity-feel-extraordinary': 'so far',
	}" />

	<p> <nao n="quantity"/> can also with used with clauses with the meaning <i>so much that…</i>: </p>

	<PAT :pairs="{
		'do_voice-find_funny quantity bodypart-container-object-eat/drink explode': 'laugh so much that one\'s stomach explodes',
	}" />

	<p> To create the comparative and superlative forms, use <nao n="more"/> = <i>more</i> and <nao n="completely-more"/> = <i> the most </i>: </p>

	<PAT :pairs="{
		'more PASSIVE-like-see/look': 'more beautiful',
		'completely-more PASSIVE-like-see/look': 'the most beautiful',
		'more good': 'better',
		'completely-more good': 'the best',
	}" />

	<p> Analogically, you can guess how to use <nao n="less"/> = <i>less</i> and <nao n="completely-less"/> = <i>the least</i>. </p>

	<p> Use the preposition <nao n="from"/> = <i>than</i> to compare with something: </p>

	<PAT :pairs="{
		'\'sara more happy from \'alis': 'Sara is happier than Alice.',
		'room me less dark from room you': 'My room is less dark than your room',
	}" />

	<p> Another useful word to express quantity/degree is  <nao n="completely"/> = <i>completely</i>: </p>

	<PAT :pairs="{
		'completely-good': '(completely good) = perfect',
		'completely-convinced': '(be completely convinced) = be 100% sure',
		'completely-not-understand': '(completely not understand) = understand 0%'
	}" />

	<p> Moreover, we have the prefixes <nao n="too_much"/> = <i>too much</i>, <nao n="too_little"/> = <i>too little</i>
		and <nao n="not-too_little"/> = <i>enough</i>: </p>

	<PAT :pairs="{
		'too_much near': 'too close',
		'too_much sleep': 'sleep too much',
