<p class="text-center text-h3">FAQ</p>

<h3>What is Nao?</h3>
<p> Nao is a constructed international language. Its features are simplicity, logic, freedom and expressiveness. It's a language with no exceptions. It's a language that can be learned in a week. </p>

<h3>Why should I take part in Nao project?</h3>
<p> Many people in the world spend years and years to learn foreign languages, primarily English. And yet many don’t achieve good language proficiency while most people stop learning in the very beginning. The reason is of course the overcomplexity of most natural languages. The problem is that languages have evolved quite chaotically. Nobody just sat down and thought them through. If the humankind wants a good language, it has to design it like it has designed all the other technological things that we use today. </p>

<p> Nao's goal is to be an easy to learn and yet a powerful language. It aims to erase the barriers between cultures with its clear structure in the vocabulary and grammar, so it’ll take you much less time to master it and yet reach such a level of fluency that you would never reach learning a foreign language. There’s also a philosophical reason to learn Nao: it helps see the world the way it is, rather than hiding the truth behind vague words as most languages often do. </p>

<h3>What’s unique about Nao?</h3>
<p> Nao has a unique powerful rich system of logical word construction. It allows one to reach a better level of expressiveness and clarity, and at the same time master the language way faster than any natural language. </p>

<p>
<b> naigedifua</b> = <i> (literally «be of such high temperature that [it] becomes gas») = boil </i>
</p><p>
<b> sobefasim </b> = <i> (literally «see/notice, and as a result, do the same») = imitate </i>
</p><p>
<b> mipaudasem </b> = <i> (literally «spouse's mother») = mother-in-law </i>
</p><p>
<b> danaponsolse </b> = <i> (literally «a person who only cares about themselves») = a selfish person </i>
</p>
<p> The grammar of Nao allows one to construct words with any desired meaning. Knowing just 300 elementary words and knowing how to combine them will allow you to construct thousands and thousands of words which is enough for full-fledged speaking. Here you can see the <router-link to="/comparison">comparison with other conlangs</router-link>. </p>

<h3>What alphabet does Nao use? </h3>
<p> Nao uses the latin alphabet: 5 vowels and 14 consonants (to transliterate foreign words, 4 more letters are used).
One letter corresponds to one sound. All words are pronounced exactly they way they are written.
You can also take a look at <router-link to="/textbook/phrases">phrases for beginners</router-link>. </p>

<h3>What about the grammar of Nao?</h3>
<p> Nao has no declensions, no conjugations. All forms are regular. The system of tenses is rich but not obligatory. By default, the word order is predetermined but the system of markers allows you to make any word order in the sentence that you wish for. </p>

<h3>Who created Nao and when? </h3>
<p> The development started in 2013 by a polyglot from Russia. The language went through a lot of reforms and versions until it finally acquired its modern form and the first speakers around 2017. </p>

<h3>Is Nao based on any language?</h3>
<p> Not really. Even though many elementary words are borrowed from the world's biggest languages like English and Chinese, their combinations don't look similar to any existing language, and moreover, the meanings of those combinations are defined by the rules, not by equating them to words of some other existing languages, which makes Nao vocabulary independent, unambiguous and culturally neutral. </p>

<h3>How many words are there in Nao?</h3>
<p> Technically infinite. It’s like asking “How many sentences are there in English?”. Just like the grammar of English allows you to generate an infinite number of sentences, the grammar of Nao goes to the next level and allows you to generate an infinite number of words. Nao is an agglutinative language which means that one can stick together as many words as necessary. </p>

<h3>Can I build my own words?</h3>
<p> Again, it’s like asking “Can I create my own sentences in English?”. Yes, you can. In fact, all the non-elementary words you see in the dictionary are just recommendations, they do not have any special status. So the words that you make are just as good and valid as the ones you see in the dictionary, as long as you carefully follow the rules of word construction. </p>

<h3>How did you decide which words would be the elementary ones?
</h3>
<p> Thousands and thousands of hours of hard work have been spent trying to determine which words should be the elementary ones. Note that Nao is not radically different in this perspective because every language has a list of elementary words - the only difference is that Nao just has less of them, without sacrificing the expressiveness. </p>

<h3>How do you decide how to construct a certain word? After all, everybody’s different, everybody thinks differently, everybody sees the world differently.</h3>
<p> Nobody decides anything. Instead, words are discovered, just like formulas are discovered in mathematics. The language has clear certain rules on word construction, and everything derives from there. Also, if two people think of the same word in different ways, it means that they put different meanings or different nuances to them, so obviously they might have different solutions, and it’s okay as long as everything follows the rules. </p>

<h3>But the language is a living thing. Isn't all its "logic" going to be destroyed as the language is used?
</h3>
<p> In the modern era of information it has become very easy to maintain and control languages. If we look 100 years back, many languages have not changed much thanks to education, the popularity of the language and its usage in mass media. Nao is even easier to remain stable for a few reasons: firstly, because any change (whether related semantics or pronunciation) will stick out because of its irregularity which will cause a chain reaction changing everything related to it which will be so noticeable bringing chaos that it will just be impossible to use. Secondly, Nao is nobody’s native language, it's a helpful second language. Logic and prescriptivism will always be the priority. Think of it as a standard version of a programming language. The authority will edit it little by little over time (mostly adding something new) following human needs. Sure there might be some slang, but most of it will come and go. </p>


